import * as React from 'react';

import { Link } from 'react-router-dom';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

import { Button, Grid, Box } from '@mui/material';

import ShoppingBagIcon from '@mui/icons-material/ShoppingBag';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';

const TAX_RATE = 0.07;

function ccyFormat(num) {
  return `${num.toFixed(2)}`;
}

function priceRow(qty, unit) {
  return qty * unit;
}

function createRow(desc, qty, unit) {
  const price = priceRow(qty, unit);
  return { desc, qty, unit, price };
}

function subtotal(items) {
  return items.map(({ price }) => price).reduce((sum, i) => sum + i, 0);
}

const rows = [
  createRow('Paperclips (Box)', 100, 1.15),
  createRow('Paper (Case)', 10, 45.99),
  createRow('Waste Basket', 2, 17.99),
];

const invoiceSubtotal = subtotal(rows);
const invoiceTaxes = TAX_RATE * invoiceSubtotal;
const invoiceTotal = invoiceTaxes + invoiceSubtotal;

const Cart = () => {
    return (
      <Grid xs={12} sx={{mt: 10, mb: 3}}>
        <Grid xs={2}>
          <TableContainer component={Paper} sx={{maxWidth: 700, ml: 'auto', mr: 'auto'}}>
            <Table sx={{ maxWidth: 700 }} aria-label="spanning table">
              <TableHead>
                <TableRow>
                  <TableCell>Producto.</TableCell>
                  <TableCell align="right">Cantidad.</TableCell>
                  <TableCell align="right">Precio</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {rows.map((row) => (
                  <TableRow key={row.desc}>
                    <TableCell>{row.desc}</TableCell>
                    <TableCell align="right">{row.qty}</TableCell>
                    <TableCell align="right">{ccyFormat(row.price)}</TableCell>
                  </TableRow>
                ))}
      
                <TableRow>
                  <TableCell>Subtotal</TableCell>
                  <TableCell rowSpan={1} />
                  <TableCell align="right">{ccyFormat(invoiceSubtotal)}</TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>Descuento</TableCell>
                  <TableCell rowSpan={1} />
                  <TableCell align="right">{`${(TAX_RATE * 100).toFixed(0)} %`}</TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>Total</TableCell>
                  <TableCell rowSpan={1} />
                  <TableCell align="right">{ccyFormat(invoiceTotal)}</TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
        <Grid xs={10} sx={{mt: 4, pl: 2}} container spacing={2}>
          <Box sx={{ml: 'auto'}}>
            <Link to="/menu" style={{textDecoration: 'none'}}>
              <Button variant="outlined" endIcon={<ShoppingBagIcon />}>
                Seguir comprando
              </Button>
            </Link>
            
            
          </Box>

          <Box sx={{mr: 'auto'}}>

            <Link to="" style={{textDecoration: 'none'}}>
              <Button variant="contained" endIcon={<AttachMoneyIcon />}>
                Confirmar
              </Button>
            </Link>
          </Box>


        </Grid>
      </Grid>
    );
}
  

export default Cart;